.nav-top {
  border-bottom: 1px solid #707070;

  svg {

    @media (min-width: 768px) {

      transform: translateY(50%);
    
    }

    width: 10vw;
    min-width: 50px;
    max-width: 80px;
    border: 1px solid #707070;
    border-radius: 100%;
    position: absolute;
    transform: translateY(65%);

    circle {
      fill:#E6E6E6;
    }

  }

  .navbar-toggler {
    border: none;

    .navbar-toggler-icon {
      background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='square' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    }

  }

}
